import { Card, Container, Row, Col } from "react-bootstrap";

function Section2(){
    const cardData = [
        {id:1, title:'  ', subTitle:'   HTML   ', tool:'html, java script, swiper', src:'http://ooaa.dothome.co.kr/', git:'https://www.figma.com/design/Rq3yUI3b0Yw1OGnOXjIB08/%EB%A9%94%EB%A6%AC%EC%B8%A0?node-id=0-1&m=dev&t=JpV8mNm2fEQ6sXkS-1', img:`${process.env.PUBLIC_URL}/images/meritz.png`},
        {id:2, title:'KCC   ', subTitle:'   HTML   ', tool:'html, java script, swiper, slick', src:'https://my-eunjin.github.io/kcc/', git:'https://github.com/my-eunjin/kcc', img:`${process.env.PUBLIC_URL}/images/kcc.png`},
        {id:3, title:' ', subTitle:'react   ', src:'https://my-eunjin.github.io/app/', tool:'react, swiper', git:'https://github.com/my-eunjin/app', img:`${process.env.PUBLIC_URL}/images/medi.png`},
        {id:4, title:' ', subTitle:'react   +', src:'https://my-eunjin.github.io/calendar-Todo/', tool:'react, tailwind', git:'https://github.com/my-eunjin/calendar-Todo', img:`${process.env.PUBLIC_URL}/images/todo.png`},
        {id:5, title:' ', subTitle:'vue     ', src:'https://my-eunjin.github.io/weather/', tool:'vue, openweathermap', git:'https://github.com/my-eunjin/weather', img:`${process.env.PUBLIC_URL}/images/weather.png`},
    ]
    return(
        <Container fluid id="work" className="fontChange p-4 bg-primary">
            <Row className="bg-primary">
            <h2 id="h2" className="text-white py-5">portfolio</h2>
                {cardData.map((card)=>(
                    <Col key={card.id} md={4} className="mb-4">
                        <Card className="card bg-secondary rounded">
                            {/* <Card.Header>{card.title}</Card.Header> */}
                            <Card.Body>
                                <Card.Title>{card.title}</Card.Title>
                                <Card.Text>{card.subTitle}</Card.Text>
                                <Card.Text> : {card.tool}</Card.Text>
                                <Card.Img src={card.img} alt={card.title} />
                                <Card.Link href={card.src} target="_blank" className="mt-3 badge rounded-pill bg-primary">VIEW</Card.Link>
                                <Card.Link href={card.git} target="_blank" className="mt-3 badge rounded-pill bg-primary">Github</Card.Link>
                            </Card.Body>
                        </Card>
                    </Col>
                ))};
            </Row>

        </Container>
    )
}

export default Section2;